<template>
  <div>
    <ul>
    <li v-for="message in messages" :key="message.id">
      <router-link :to="{
        name: 'xiangqing',
        query: {
          id: message.id,
          content: message.msg
        }
      }">{{ message.msg }}</router-link>
      <button @click="pushCheck(message)">Push check</button>
      <button @click="replaceCheck(message)">Replace check</button>
      <!-- <a href="#">{{ message.msg }}</a> -->
    </li>
  </ul>
  <br>
  <router-view></router-view>
  </div>
</template>

<script>
  import Detail from '../pages/Detail.vue'

  export default {
    name: 'Messages',
    data(){
      return {
        messages: [
          {id: '001', msg: 'message001'}, 
          {id: '002', msg: 'message002'},
          {id: '003', msg: 'message003'},
        ]
      }
    },
    components: {Detail},
    mounted(){
      console.log(this.$route);
    },
    methods: {
      pushCheck(m){
        this.$router.push({
          name: 'xiangqing',
          query: {
            id: m.id,
            content: m.msg
          }
        })
      },
      replaceCheck(m){
        this.$router.replace({
          name: 'xiangqing',
          query: {
            id: m.id,
            content: m.msg
          }
        })
      },
    }
  }
</script>

