import VueRouter from 'vue-router'
import About from '../pages/About'
import Home from '../pages/Home'
import News from '../pages/News'
import Messages from '../pages/Messages'
import Detail from '../pages/Detail'

const router =  new VueRouter({
  routes: [
    {
      path: '/about',
      component: About,
      meta: {
        title: 'About'
      }
    },
    {
      path: '/home',
      component: Home,
      meta: {
        title: 'Home'
      },
      children: [
        {
          path: 'news',
          component: News,
          meta: {
            isAuth: true,
            title: 'News'
          },
        },
        {
          path: 'messages',
          component: Messages,
          meta: {
            title: 'Messages'
          },
          children: [
            {
              name: 'xiangqing',
              path: 'details',
              component: Detail,
              meta: {
                isAuth: true,
                title: 'Details'
              },
              props: function($route){
                return {
                  id: $route.query.id,
                  content: $route.query.content
                }
              }
            }
          ]
        },
      ]
    },
  ]
})

// 全局前置路由守卫，在初始化和每一次路由切换之前调用函数
router.beforeEach((to, from, next) => {
  if(to.meta.isAuth){
    if(localStorage.getItem('school') === 'Yui'){
      next()
    }else{
      alert('You have no permission to access this information!');
    }
  }else{
    next()
  }
})

router.afterEach((to, from) => {
  document.title = to.meta.title || 'Yui System'
})

export default router