<template>
  <ul>
    <li v-for="(news,index) in newsList" :key="index">{{ news }}</li>
  </ul>
</template>

<script>
  export default {
    name: 'News',
    data(){
      return {
        newsList: ['news1', 'news2', 'news3']
      }
    },

    // 组件内路由守卫
    beforeRouteEnter(to, from, next){
      if(to.meta.isAuth){
        if(localStorage.getItem('school') === 'Yui'){
          next();
        }else{
          alert('You have no permission to access this information!');
        }
      }else{
        next();
      }
    },
    beforeRouteLeave(to, from, next){
      console.log('News--beforeRouterLeave', to, from);
      next();
    }
  }
</script>

